export default class MessagesListItem { // extends React.PureComponent<MessagesListProps> {
    static count = 0;

    private static makeParticipantsAvatar(participants: MessagesParticipant[]) {
        switch (participants.length) {
            case 0: // ERROR: no one in the conversation??
                return (<></>);
            case 1: // you're talking to yourself
                return (
                    <div className="messages-list-item-avatar">
                        <img alt={participants[0].displayName} title={`${participants[0].displayName} \n@${participants[0].userName}`} src={participants[0].avatarDataUri} />
                    </div>
                );
            case 2: // first participant is you, show the others
                return (
                    <div className="messages-list-item-avatar">
                        <img alt={participants[1].displayName} title={`${participants[1].displayName} \n@${participants[1].userName}`} src={participants[1].avatarDataUri} />
                    </div>
                );
            case 3: // first participant is you, show the others
                return (
                    <div className="messages-list-item-avatar">
                        <div className="messages-list-item-avatar-1of2">
                            <img alt={participants[1].displayName} title={`${participants[1].displayName} \n@${participants[1].userName}`} src={participants[1].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-2of2">
                            <img alt={participants[2].displayName} title={`${participants[2].displayName} \n@${participants[2].userName}`} src={participants[2].avatarDataUri} />
                        </div>
                    </div>
                );
            case 4: // first participant is you, show the others
                return (
                    <div className="messages-list-item-avatar">
                        <div className="messages-list-item-avatar-1of3">
                            <img alt={participants[1].displayName} title={`${participants[1].displayName} \n@${participants[1].userName}`} src={participants[1].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-2of3">
                            <img alt={participants[2].displayName} title={`${participants[2].displayName} \n@${participants[2].userName}`} src={participants[2].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-3of3">
                            <img alt={participants[3].displayName} title={`${participants[3].displayName} \n@${participants[3].userName}`} src={participants[3].avatarDataUri} />
                        </div>
                    </div>
                );
            case 5: // first participant is you, show the others
                return (
                    <div className="messages-list-item-avatar">
                        <div className="messages-list-item-avatar-1of4">
                            <img alt={participants[1].displayName} title={`${participants[1].displayName} \n@${participants[1].userName}`} src={participants[1].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-2of4">
                            <img alt={participants[2].displayName} title={`${participants[2].displayName} \n@${participants[2].userName}`} src={participants[2].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-3of4">
                            <img alt={participants[3].displayName} title={`${participants[3].displayName} \n@${participants[3].userName}`} src={participants[3].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-4of4">
                            <img alt={participants[4].displayName} title={`${participants[4].displayName} \n@${participants[4].userName}`} src={participants[4].avatarDataUri} />
                        </div>
                    </div>
                );
            default: // first participant is you, show three, then more symbol
                return (
                    <div className="messages-list-item-avatar">
                        <div className="messages-list-item-avatar-1of4">
                            <img alt={participants[1].displayName} title={`${participants[1].displayName} \n@${participants[1].userName}`} src={participants[1].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-2of4">
                            <img alt={participants[2].displayName} title={`${participants[2].displayName} \n@${participants[2].userName}`} src={participants[2].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-3of4">
                            <img alt={participants[3].displayName} title={`${participants[3].displayName} \n@${participants[3].userName}`} src={participants[3].avatarDataUri} />
                        </div>
                        <div className="messages-list-item-avatar-4of4">
                            <img alt={`${participants.length} participants`} title={`${participants.length} participants`}
                                src="data:image/png;base64,"
                            />
                        </div>
                    </div>
                );
        }
    }

    public static listItemIndex = 0;
    public static render(participants: MessagesParticipant[], selectConversation: any, selectedConversationIndex: number, isUnread: boolean, userName: string) {
        let names = "";
        let count = 0;
        for (let participant of participants) {
            if (count++) {
                names += (names ? ", " : "") + participant.displayName;
            }
        }
        const index = MessagesListItem.listItemIndex;
        const active = index === selectedConversationIndex ? " active" : "";
        const unread = isUnread ? " unread" : "";
        return (
            <div className={"messages-list-item" + active } onClick={(e) => { selectConversation(e, index); } } key={1000 + MessagesListItem.listItemIndex++}>
                {MessagesListItem.makeParticipantsAvatar(participants)}
                <div className={"messages-list-item-unread" + unread}></div>
                <div className="messages-list-item-participants">{names}</div>
                <div className="messages-list-item-message"></div>
            </div>
        );

    };
};

