import * as React from 'react';
import StreamItem from '../../store/StreamItem';
import { Row, Col, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Button, Nav, NavItem, NavLink } from 'reactstrap';
import { format, formatDistanceToNow } from 'date-fns';
import './StreamContent.css';
import AuthUtil from '../../store/AuthUtil';

export default class StreamContent {
    public static render(items: StreamItem[], userName: string, startRenderIndex?: number): React.ReactFragment {
        startRenderIndex = isNaN(startRenderIndex as number) ? 0 : startRenderIndex as number;
        return (
            <div className='stream-container'>
                <div className='stream-content'>
                    {items.map((item: StreamItem, index: number) => {
                        if (index < (startRenderIndex as number)) { return (<div data-key={ item.id }></div>); }
                        return (
                            <>
                                {StreamContent.renderAd(StreamContent.getAdStreamItem(), index % 12)}
                                <div className='stream-item' data-key={item.id}>
                                    <Row key={item.id + "-content"}>
                                        <Col md='2'>
                                            <img className='stream-item-avatar' alt='user avatar' src={item.avatarDataUri} />
                                        </Col>
                                        <Col md='10'>
                                            <div>
                                                <div className='stream-item-display-name'><a href={`/user/${item.userName}`}>{item.displayName}</a></div>
                                                <div className='stream-item-user-name'><a href={`/user/${item.userName}`}>@{item.userName}</a></div>
                                                <div className='stream-item-post-date'>{formatDistanceToNow(Date.parse(item.date))} ago, {format(Date.parse(item.date), 'PPPp')}</div>
                                            </div>
                                            <div className='stream-item-summary'>{ item.summary }</div>
                                        </Col>
                                    </Row>
                                    <Row className='stream-item-buttons' key={item.id + "-nav"}>
                                        <Col md='2'>&nbsp;</Col>
                                        <Col md='2'><i className="bi bi-chat-right-text">&nbsp;</i>&nbsp;<span className="stream-item-score">{ item.replyCount }</span></Col>
                                        <Col md='2'><i className="bi bi-arrow-repeat">&nbsp;</i>&nbsp;<span className="stream-item-score">{item.shareCount}</span></Col>
                                        <Col md='2'><i className="bi bi-hand-thumbs-up">&nbsp;</i>&nbsp;<span className="stream-item-score">{item.likeCount}</span></Col>
                                        <Col md='2'><i className="bi bi-hand-thumbs-down">&nbsp;</i>&nbsp;<span className="stream-item-score">{item.dislikeCount}</span></Col>
                                        <Col md='2'>
                                            <UncontrolledDropdown setActiveFromChild>
                                                <DropdownToggle tag="a" className="nav-link stream-item-score-more">
                                                    <span className="stream-item-score-more"><i className="bi bi-three-dots">&nbsp;</i></span>
                                                </DropdownToggle>
                                                <DropdownMenu>
                                                    <DropdownItem tag="a" href="/blah" key={item.id + "-add-bookmark"}><i className="bi bi-bookmark-plus">&nbsp;</i> Add Bookmark</DropdownItem>
                                                    <DropdownItem tag="a" href="/blah" key={item.id + "-add-list"}><i className="bi bi-journal-plus">&nbsp;</i> Add to List</DropdownItem>
                                                    <DropdownItem tag="a" href="/blah" key={item.id + "-embed"}><i className="bi bi-code-slash">&nbsp;</i> Embed Post</DropdownItem>
                                                    <DropdownItem tag="a" href="/blah" key={item.id + "-analytics"}><i className="bi bi-graph-up-arrow">&nbsp;</i> Analytics</DropdownItem>
                                                    <DropdownItem divider />
                                                    <DropdownItem tag="a" href="/blah" key={item.id + "-flag"}><i className="bi bi-flag">&nbsp;</i> Flag Content</DropdownItem>
                                                </DropdownMenu>
                                            </UncontrolledDropdown>
                                        </Col>
                                    </Row>
                                    <hr />
                                </div>
                            </>
                        );}
                    )}
                </div>
            </div>
        );
    }

    static adUserNames: string[] = ['fauxcabulary', 'hiive', 'ososoft', "lanierconsulting", "rjwallace", "myschoolyear"];
    static adUserGuids: string[] = ['72c50c63-f2dd-41c4-8930-c496098eb3e5', '72c50c63-f2dd-41c4-8930-c496098eb3d3', '72c50c63-f2dd-41c4-8930-c496098eb3f4', '72c50c63-f2dd-41c4-8930-c496098eb4f5', '72c50c63-f2dd-41c4-8930-c496098eb333', '72c50c63-f2dd-41c4-8930-c496098eb222'];
    static adUserDisplayNames: string[] = ['Fauxcabulary.org', 'Hiive.com', 'Ososoft.net', 'LanierConsulting.net', 'RWallaceConsulting.com', 'MySchoolYear.com'];
    static adUserSites: string[] = ['http://fauxcabulary.org', 'http://hiive.com', 'http://ososoft.net', 'http://lanierconsulting.net', 'http://rwallaceconsulting.com', 'http://myschoolyear.com'];
    static adAvatarDataUris: string[] = [
        "data:image/png;base64,",
        "data:image/png;base64,",
        "data:image/png;base64,",
        "data:image/png;base64,",
        "data:image/png;base64,",
        "data:image/png;base64,",
    ];

    // static adIndex: number = -1;
    public static getAdStreamItem(): StreamItem {
        const adIndex = Math.floor(Math.random() * StreamContent.adUserNames.length);
        const adImage = StreamContent.adUserNames[adIndex] === "rjwallace" ? 
            `/images/ads/ad-rwallaceconsulting.png` :
            `/images/ads/ad-${StreamContent.adUserNames[adIndex]}.png`;
        return {
            id: StreamContent.adUserGuids[adIndex],
            // StreamType = $"user/{ UserNames[ndx] }",
            userName: `@${StreamContent.adUserNames[adIndex]}`,
            displayName: `${StreamContent.adUserDisplayNames[adIndex]}`,
            date: new Date(),
            avatarDataUri: StreamContent.adAvatarDataUris[adIndex],
            summary: "",
            media: adImage,
            replyCount: 20,
            shareCount: 84,
            likeCount: 47,
            dislikeCount: 21,
            flagCount: 0,
            isPinned: false,
            isPromoted: true,
            replyTo: "",
            adLink: StreamContent.adUserSites[adIndex],
        } as unknown as StreamItem;
    }

    public static renderAd(item: StreamItem, index: number): React.ReactFragment {
        if(index !== 11) { return (<div key={item.id}></div>); }
        return (
            <div className='stream-item' key={item.id + "-stream-item"}>
                <Row key={item.id + "-content"}>
                    <Col md='2'>
                        <img className='stream-item-avatar' alt='user avatar' src={item.avatarDataUri} />
                    </Col>
                    <Col md='10'>
                        <div>
                            <div className='stream-item-display-name'>{item.displayName}</div>
                            <div className='stream-item-user-name'><i className="bi bi-arrow-up-right-square"></i> Promoted by {item.userName}</div>
                        </div>
                        <a href={item.adLink} target='_blank'><div className='stream-item-summary'><img alt="advertisment" src={item.media} style={{width : "99.9%"}} /></div></a>
                    </Col>
                </Row>
                <Row className='stream-item-buttons' key={item.id + "-nav"}>
                    <Col md='2'>&nbsp;</Col>
                    <Col md='2'><i className="bi bi-chat-right-text">&nbsp;</i> { item.replyCount }</Col>
                    <Col md='2'><i className="bi bi-arrow-repeat">&nbsp;</i> { item.shareCount }</Col>
                    <Col md='2'><i className="bi bi-hand-thumbs-up">&nbsp;</i> { item.likeCount }</Col>
                    <Col md='2'><i className="bi bi-hand-thumbs-down">&nbsp;</i> { item.dislikeCount }</Col>
                    <Col md='2'>
                        <UncontrolledDropdown setActiveFromChild>
                            <DropdownToggle tag="a" className="nav-link">
                                <i className="bi bi-three-dots">&nbsp;</i>
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem tag="a" href="/blah" key={item.id + "-add-bookmark"}><i className="bi bi-bookmark-plus">&nbsp;</i> Add Bookmark</DropdownItem>
                                <DropdownItem tag="a" href="/blah" key={item.id + "-add-list"}><i className="bi bi-journal-plus">&nbsp;</i> Add to List</DropdownItem>
                                <DropdownItem tag="a" href="/blah" key={item.id + "-embed"}><i className="bi bi-code-slash">&nbsp;</i> Embed Post</DropdownItem>
                                <DropdownItem tag="a" href="/blah" key={item.id + "-analytics"}><i className="bi bi-graph-up-arrow">&nbsp;</i> Analytics</DropdownItem>
                                <hr/>
                                <DropdownItem tag="a" href="/blah" key={item.id + "-flag"}><i className="bi bi-flag">&nbsp;</i> Flag Content</DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </Col>
                </Row>
                <hr />
            </div>
        );
    }

    private static renderHeaderButton(userName: string, isSelf: boolean) {
        if (isSelf) {
            return (<Button id="cmdFollowProfile" className={`${AuthUtil.HideClassNameIfNotLoggedIn()}`} color="primary" outline><i className="bi bi-pencil-fill"></i> Edit Profile</Button>);
        } else {
            return (<Button id="cmdFollowProfile" className={`${AuthUtil.HideClassNameIfNotLoggedIn()}`} color="primary">Follow</Button>);
        }
    }

    public static renderHeader(item: StreamItem, userName: string, renderProfileHeader: boolean): React.ReactFragment {
        if (!item) { return (<></>); }
        const isSelf = userName === AuthUtil.getUserName();
        return (
            <div key={item.id}>
                <div className="profile-header">
                    <Row>
                        <Col md='12'>
                            <img className="header" src={`/images/headers/${userName}.png`} alt="profile header" />
                            <img className="avatar" src={`/images/avatars/${userName}.png`} alt="profile avatar" />
                        </Col>
                    </Row>
                    <Row className="header-name">
                        <Col md='9'>
                            <div className="header-name-display">{item.displayName}</div>
                            <div className="header-name-user">@{userName}</div>
                        </Col>
                        <Col md='3'>
                            {StreamContent.renderHeaderButton(userName, userName === AuthUtil.getUserName())}
                        </Col>
                    </Row>
                    <Row className="header-bio">
                        <Col md='12'>
                            <div className="header-bio-text">{item.summary}</div>
                        </Col>
                    </Row>
                    <Row className="header-meta">
                        <Col md='12'>
                            <i className="bi bi-geo-alt"></i>&nbsp;<span id="lblLocation">{item.replyTo}</span>
                            <i className="bi bi-link"></i>&nbsp;<span id="lblWebsite">{item.adLink}</span>
                            <i className="bi bi-balloon"></i>&nbsp;<span id="lblBirthdate">{item.avatarDataUri}</span>
                            <i className="bi bi-trophy"></i>&nbsp;<span id="lblScore">2.4k</span>
                        </Col>
                    </Row>
                    <Row className="header-meta">
                        <Col md='12'>
                            <i className="bi bi-person-square"></i>&nbsp;<span id="lblJoinedOn">Joined {item.media}</span>
                            <i className="bi bi-people-fill"></i>&nbsp;<span id="lblFollowers">Followers {item.likeCount}</span>
                            <i className="bi bi-person-heart"></i>&nbsp;<span id="lblFollowing">Following {item.shareCount}</span>
                        </Col>
                    </Row>
                    <Row className="header-meta">
                        <Col md='12'>
                            <Nav tabs>
                                <NavItem>
                                    <NavLink className="active" onClick={(e) => { }}><i className="bi bi-card-text"></i> Posts</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="" onClick={(e) => { }}><i className="bi bi-reply-all-fill"></i> Posts &amp; Replies</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="" onClick={(e) => { }}><i className="bi bi-collection-play-fill"></i> Media</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="" onClick={(e) => { }}><i className="bi bi-activity"></i> Activity</NavLink>
                                </NavItem>
                            </Nav>
                        </Col>
                    </Row>
                </div>
            </div>
        );
    }
}

