import * as React from 'react';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Row, Col } from 'reactstrap';
import { ApplicationState } from '../store';
import * as LoginStore from '../store/Login';
import SidebarLeft from './stream-items/SidebarLeft';
import App from '../App';
import AuthUtil from '../store/AuthUtil';
import { Redirect } from 'react-router-dom';

type LoginProps =
    LoginStore.LoginState &
    typeof LoginStore.actionCreators &
    RouteComponentProps<{}>;

class FetchData extends React.PureComponent<LoginProps> {

    private renderLoginRedirecText() {
        if (AuthUtil.isLoggedIn()) {
            return (
                <>
                    <p>If you want to carry on without providing<br />
                       feedback, you can do one of the following:</p>
                    <ul>
                        <li>Use your browser's back button to return<br />
                            to the previous page.</li>
                        <li>Select one of the navigation items to the<br />
                            left of this page.</li>
                        <li>Click the logo link at the top, left of this <br />page to see what all the buzz is about.<br />
                            &nbsp;&nbsp;&nbsp;<a className="navbar-brand" href="/buzz"><img src="data:image/png;base64," alt="hearder logo thumbnail" /></a></li>
                    </ul>
                </>
            );
        } else {
            return (
                <>
                    <p>Click the logo link at the top, left of this page <br />
                       to see what all the buzz is about.</p>
                    <p>&nbsp;&nbsp;&nbsp;<a className="navbar-brand" href="/buzz"><img src="data:image/png;base64," alt="hearder logo thumbnail" /></a></p>
                </>
            );
        }
    }

    // This method is called when the component is first added to the document
    public render() {
        return (
            <Row className='tall'>
                <Col md='3'> <SidebarLeft> </SidebarLeft> </Col>
                <Col md='9'>
                    <h2>Page Not Found (404)</h2>
                    <p>The page you're trying to view does not exist.<br />
                       If you feel that this is a problem with the site,<br />
                       please <a href="#">click here</a> to report the issue.</p>

                    {this.renderLoginRedirecText()}
                </Col>
                <Col md='4'>&nbsp;</Col>
            </Row>
        );
    };
};

export default connect(
    (state: ApplicationState) => state.login,
    LoginStore.actionCreators
)(FetchData as any);

